/* Neuro */
.neuro.calendar {
    margin-top: 15px;
    position: absolute;
    padding: 20px 20px;
    -webkit-border-radius: 20px;
    border-radius: 20px;
    background: #ebecf0;
    -webkit-box-shadow: 6px 6px 8px #dfe0e4, -6px -6px 8px #f7f8fc;
    box-shadow: 6px 6px 8px #dfe0e4, -6px -6px 8px #f7f8fc;
    width: fit-content;
    width: 300px;
    height: fit-content;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    z-index: 10;
}

.neuro .daysContainer {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    box-shadow: none
}

.neuroDay {
    border-radius: 25%;
    box-shadow: 0px 0px 0px #dfe0e4, -0px -0px 0px #f7f8fc;
    transition: box-shadow 0.3s ease;
}

.neuroDay:hover {
    background: #ebecf0;
    -webkit-box-shadow: 6px 6px 8px #dfe0e4, -6px -6px 8px #f7f8fc;
    box-shadow: 6px 6px 8px #dfe0e4, -6px -6px 8px #f7f8fc;
}

.neuroYear,
.neuroMonth {
    border-radius: 10%;
    box-shadow: 0px 0px 0px #dfe0e4, -0px -0px 0px #f7f8fc;
    transition: box-shadow 0.3s ease;
}

.neuroYear:hover,
.neuroMonth:hover {
    background: #ebecf0;
    -webkit-box-shadow: 6px 6px 8px #dfe0e4, -6px -6px 8px #f7f8fc;
    box-shadow: 6px 6px 8px #dfe0e4, -6px -6px 8px #f7f8fc;
}

.neuroActive,
.neuroToday {
    border-radius: 25%;
    background: #ebecf0;
    -webkit-box-shadow: 6px 6px 8px #dfe0e4, -6px -6px 8px #f7f8fc;
    box-shadow: 6px 6px 8px #dfe0e4, -6px -6px 8px #f7f8fc;
}

.neuroToday {
    background: #c6c6c6;
}